import * as Blockly from 'blockly/core';
import { javascriptGenerator } from 'blockly/javascript';

// JSON
let blockname="b_load_char"
// 
const jsondesc = {
    "type": `${blockname}`,
    "message0": " %1  %2  %3 () %4 %5",
    "args0": [
      {
        "type": "input_value",
        "name": "val1",
        "check": "String"
      },
      {
        "type": "field_number",
        "name": "num1",
        "min": 0,
        "value": 2.7,
        "max": 1000,
        "precision": 0.1,
      },
      {
        "type": "input_value",
        "name": "val2",
        "check": "String"
      },
      {
        "type": "input_value",
        "name": "val3",
        "check": "String"
      },
      {
        "type": "field_dropdown",
        "name": "drop1",
        "options": [
            ["","char"],
            ["","charC"]
        ]
      },
    ],
    "inputsInline": true,
    "previousStatement": null,
    "nextStatement": null,
    "colour": 260,
    "tooltip": "",
    "helpUrl": ""
  }

// 
Blockly.Blocks[blockname] = {
    init: function () {
        this.jsonInit(jsondesc);
    }
}

// js
javascriptGenerator[blockname] = function (block) {
    const value_val1 = javascriptGenerator.valueToCode(block, 'val1', javascriptGenerator.ORDER_ATOMIC); //
    const value_val2 = javascriptGenerator.valueToCode(block, 'val2', javascriptGenerator.ORDER_ATOMIC); // 
    let value_val3 = javascriptGenerator.valueToCode(block, 'val3', javascriptGenerator.ORDER_ATOMIC);// 

    const dropdown_drop1 = block.getFieldValue('drop1');//

    const number_num1 = block.getFieldValue('num1');//

    if(value_val1.length==0||value_val2.length==0||value_val3.length==0){
      // 
      return ``
    }

    // 
    value_val3=value_val3.split(" ").join(",")


    return `
if(importArea){
    stagelist.push(\`load ${dropdown_drop1} \${${value_val1}} ${number_num1} \${${value_val2}} [\${${value_val3}}]\`);
}
`

}

