import * as Blockly from 'blockly/core';
import { javascriptGenerator } from 'blockly/javascript';
import { students_datamap } from '../../datamap';

// JSON

// 
let blockname="b_stu_speak"
const jsondesc = {
    "type": `${blockname}`,
    "message0": " %1  %2  %3  %4",
    "args0": [
      {
        "type": "input_value",
        "name": "val1",
        "check": "String"
      },
      {
        "type": "input_value",
        "name": "val2",
        "check": "String"
      },
      {
        "type": "field_dropdown",
        "name": "drop1",
        "options": [
            ["","none"],
            ["","Action"],
            ["","Aggro"],
            ["","Anxiety"],
            ["","Chat"],
            ["!","E"],
            ["?!","EQ"],
            ["?","Q"],
            ["","Heart"],
            ["...","Idea"],
            ["","Note"],
            ["","Shy"],
            ["","Sweat"],
            ["","Twinkle"],
            ["","Bulb"],
            ["","Sad"],
            ["","Sigh"],
            ["","Tear"],
            ["","Steam"],
        ]
      },
      {
        "type": "field_dropdown",
        "name": "drop2",
        "options": [
            ["","none"],
            ["","down"],
            ["","up"],
            ["","empty"],
            ["","close"],
            ["","back"],
            ["","nod"],
            ["","jump"],
            ["","sshake"],
            ["","bshake"]
        ]
      },
    ],
    "inputsInline": true,
    "previousStatement": null,
    "nextStatement": null,
    "colour": 120,
    "tooltip": "",
    "helpUrl": ""
  }

// 
Blockly.Blocks[blockname] = {
    init: function () {
        this.jsonInit(jsondesc);
    }
}

// js
javascriptGenerator[blockname] = function (block) {
    const stuname = javascriptGenerator.valueToCode(block, 'val1', javascriptGenerator.ORDER_ATOMIC);//
    const face = javascriptGenerator.valueToCode(block, 'val2', javascriptGenerator.ORDER_ATOMIC);//

    const emo = block.getFieldValue('drop1');//
    const action=block.getFieldValue("drop2");//

    // 

    let finalcode=""
    if(!stuname){
        return ''
    }
    // 
    if(emo!=="none"){
        finalcode+=`
stagelist.push(\`\${${stuname}} emo ${emo}\`);
`
    }
    // 
    if(action==="down"){
        finalcode+=`
stagelist.push(\`\${${stuname}} down\`);
`
    }else if(action==="up"){
        finalcode+=`
stagelist.push(\`\${${stuname}} up\`);
`
    }else if(action==="empty"){
        finalcode+=`
stagelist.push(\`\${${stuname}} empty\`);
`
    }else if(action==="close"){
        finalcode+=`
stagelist.push(\`\${${stuname}} close\`);
`
    }else if(action==="back"){
        finalcode+=`
stagelist.push(\`\${${stuname}} back\`);
`
    }else if(action==="nod"){
        finalcode+=`
stagelist.push(\`\${${stuname}} shakeY 10 -5 1\`);
`
    }else if(action==="jump"){
        finalcode+=`
stagelist.push(\`\${${stuname}} shakeY 10 4 1\`);
`
    }else if(action==="sshake"){
        finalcode+=`
stagelist.push(\`\${${stuname}} shakeX 15 1 6\`);
`
    }else if(action==="bshake"){
        finalcode+=`
stagelist.push(\`\${${stuname}} shakeX 20 2 6\`);
`
    }
    // 
    if(face!=="''"){
        finalcode+=`
stagelist.push(\`\${${stuname}} state \${${face}}\`);
`
    }

    return finalcode;
    // return `stagelist.push(\`\${${stuname}} ${dropdown_drop1}\`);`
}

