import * as Blockly from 'blockly/core';
import { javascriptGenerator } from 'blockly/javascript';

// JSON
let blockname="b_stu_shake"
// 
const jsondesc = {
    "type": `${blockname}`,
    "message0": " %1  %2   %3  %4  %5 ",
    "args0": [
      {
        "type": "input_value",
        "name": "val1",
        "check": "String"
      },
      {
        "type": "field_dropdown",
        "name": "drop1",
        "options": [
            ["","shakeX"],
            ["","shakeY"],
        ]
      },
      {
        "type": "field_number",
        "name": "num1",
        "min": 0.1,
        "value": 10,
        "max": 1000,
        "precision": 0.1,
      },
      {
        "type": "field_number",
        "name": "num2",
        "min": -1000,
        "value": 0.1,
        "max": 1000,
        "precision": 0.1,
      },
      {
        "type": "field_number",
        "name": "num3",
        "min": 0,
        "value": 5,
        "max": 10000000,
        "precision": 0.1,
      },
    ],
    "inputsInline": true,
    "previousStatement": null,
    "nextStatement": null,
    "colour": 120,
    "tooltip": "",
    "helpUrl": ""
  }

// 
Blockly.Blocks[blockname] = {
    init: function () {
        this.jsonInit(jsondesc);
    }
}

// js
javascriptGenerator[blockname] = function (block) {
    const value_val1 = javascriptGenerator.valueToCode(block, 'val1', javascriptGenerator.ORDER_ATOMIC);
    if(value_val1.length==0){
      // 
      return ``
    }
    const dropdown_drop1 = block.getFieldValue('drop1');
    const number_num1 = block.getFieldValue('num1');
    const number_num2 = block.getFieldValue('num2');
    const number_num3 = block.getFieldValue('num3');



    return `stagelist.push(\`\${${value_val1}} ${dropdown_drop1} ${number_num1} ${number_num2} ${number_num3*2}\`);`
}

