import * as Blockly from 'blockly/core';
import { javascriptGenerator } from 'blockly/javascript';

// JSON
let blockname="b_bgm_control"
// 
const jsondesc = {
    "type": `${blockname}`,
    "message0": " %1  %2  %3 %4",
    "args0": [
      {
        "type": "input_value",
        "name": "val1",
        "check": "String"
      },
      {
        "type": "field_dropdown",
        "name": "drop1",
        "options": [
            ["","bgm"],
            ["","se"],
        ]
      },
      {
        "type": "field_dropdown",
        "name": "drop2",
        "options": [
            ["()","once"],
            ["()","loop"],
        ]
      },
      {
        "type": "field_dropdown",
        "name": "drop3",
        "options": [
            ["","play"],
            ["","pause"],
        ]
      },
    ],
    "inputsInline": true,
    "previousStatement": null,
    "nextStatement": null,
    "colour": 65,
    "tooltip": "",
    "helpUrl": ""
  }

// 
Blockly.Blocks[blockname] = {
    init: function () {
        this.jsonInit(jsondesc);
    }
}

// js
javascriptGenerator[blockname] = function (block) {
    const value_val1 = javascriptGenerator.valueToCode(block, 'val1', javascriptGenerator.ORDER_ATOMIC);
    if(value_val1.length==0){
      // 
      return ``
    }
    const dropdown_drop1 = block.getFieldValue('drop1');// 
    const dropdown_drop2 = block.getFieldValue('drop2');// 
    const dropdown_drop3 = block.getFieldValue('drop3');// 



    return `
stagelist.push(\`${dropdown_drop1} set \${${value_val1}}\`);// 
stagelist.push(\`${dropdown_drop1} ${dropdown_drop2}\`);// 
stagelist.push(\`${dropdown_drop1} ${dropdown_drop3}\`);// 
stagelist.push(\`${dropdown_drop1} v 0.3\`);// 

`
}

